@use "../component/navigation";
@use "../abstract/index" as *;

body {
  min-height: 100vh;
  font-family: "Barlow", sans-serif;
  background: url(../../images/technology/background-technology-mobile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top left;
}

.container {
  margin-inline: auto;
  text-align: center;
  padding-bottom: 32px;
}

.technology__subtitle {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  text-transform: uppercase;
  font-weight: normal;
  color: color(secondary-color);
  margin-bottom: 32px;
}

.technology__subtitle--number {
  margin-right: 16px;
  opacity: 0.25;
  font-weight: 700;
}

.technology__image {
  img {
    height: clamp(170px, 45.34vw, 310px);
  }
}

.technology__nav__list {
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 16px;
  margin: 34px auto 26px auto;
}

.technology__nav__link {
  font-family: "Bellefair", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: color(secondary-color);
  background: transparent;
  border: none;
  border: 1px solid rgba(#fff, 0.25);
  cursor: pointer;
  padding: 0;
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: all 0.25s ease-in-out;

  &.active {
    opacity: 1;
    background: color(secondary-color);
    color: #0b0d17;
  }

  &:hover,
  &:focus-visible {
    border: 1px solid #fff;
  }
}

.technology__title {
  color: color(secondary-color);
  text-transform: uppercase;
}

.technology__title--terminology {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: normal;
  letter-spacing: 2.3625px;
  color: color(accent-color);
  margin-bottom: 9px;
}

.technology__title--name {
  font-family: "Bellefair", sans-serif;
  font-size: 24px;
  line-height: 28px;
  font-weight: normal;
}

.technology__text {
  margin-top: 16px;
  font-family: "Barlow", sans-serif;
  font-size: 15px;
  line-height: 25px;
  color: color(accent-color);
  padding-inline: 24px;
}

@keyframes zoomIn {
  0% {
    transform-origin: center;
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.zoomIn {
  animation: zoomIn 0.5s;
}
.fadeIn {
  animation: fadeIn 0.5s;
}

@media (min-width: 768px) {
  body {
    background-image: url(../../images/technology/background-technology-tablet.jpg);
  }

  .container {
    padding: 40px 0;
  }

  .technology__subtitle {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
    text-align: left;
    margin: 0 0 60px 39px;
  }

  .technology__image {
    img {
      // height: 40.37vw;
      width: 100%;
    }
  }

  .technology__nav__list {
    margin-top: 56px;
    margin-bottom: 44px;
  }

  .technology__nav__link {
    width: 60px;
    height: 60px;
    font-size: 24px;
    line-height: 28px;
  }

  .technology__title--terminology {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    margin-bottom: 11px;
  }

  .technology__title--name {
    font-size: 40px;
    line-height: 46px;
  }

  .technology__text {
    font-size: 17px;
    line-height: 28px;
    padding-inline: 155px;
  }
}

@media (min-width: 1100px) {
  body {
    padding: 0.05px;
    background-image: url(../../images/technology/background-technology-desktop.jpg);
  }

  .container {
    padding-top: 76px;
    padding-bottom: 0;
    display: grid;
    padding-left: 68px;
    grid-template-columns: 130px 470px 1fr;
    grid-template-rows: 171px 517px;
    position: relative;
    text-align: left;

    @media (min-width: 1245px) {
      padding-left: 11.5625%;
      grid-template-columns: 160px 470px 1fr;
    }
  }

  .technology__subtitle {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    margin: 0;
    padding: 0;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;
  }

  .technology__subtitle--number {
    margin-right: 24px;
  }

  .technology__nav {
    grid-column: 1 / 2;
    grid-row: 2 / -1;
    align-self: start;
  }

  .technology__nav__list {
    flex-direction: column;
    column-gap: 0;
    row-gap: 32px;
    align-items: flex-start;
    margin: 0;
  }

  .technology__nav__link {
    width: 80px;
    height: 80px;
    font-size: 32px;
    line-height: 37px;
  }

  .technology__content {
    grid-column: 2 / 3;
    grid-row: 2 / -1;
  }

  .technology__title--name {
    font-size: 56px;
    line-height: 64px;
  }

  .technology__text {
    padding: 0;
    font-size: 18px;
    line-height: 32px;
    max-width: 444px;
  }

  .technology__image {
    grid-column: 3 / -1;
    height: 527px;
    margin-top: 60px;
    right: 0;
    max-width: 515px;
    margin-left: 5vw;

    @media (min-width: 1320px) {
      margin-left: 9.027vw;
    }

    img {
      width: 100%;
      height: 527px;
    }
  }
}
