@use "../abstract/functions" as *;

.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1.5rem;
}

.header__logo {
  width: 40px;
  height: 40px;
  background: url(../../images/shared/logo.svg) no-repeat;
  background-position: center;
  background-size: cover;
}

.hamburger__container {
  padding: 0;
  margin: 0;
  border: none;
  background: transparent;
  display: block;
  position: relative;
  width: 24px;
  height: 21px;
  cursor: pointer;
  transform: scale(1);
  transition: transform 0.1s ease-in;
  z-index: 100;

  &:hover,
  &:focus-visible {
    transform: scale(1.1);
  }

  .open {
    background: transparent;
    transform: rotate(360deg);

    &:before {
      inset: auto;
      top: 0;
      left: 0;
      transform: rotate(45deg);
    }

    &:after {
      inset: auto;
      bottom: 0;
      left: 0;
      transform: rotate(-45deg);
    }
  }
}

.hamburger__btn {
  width: 24px;
  height: 3px;
  position: absolute;
  top: 9px;
  right: 0;
  background-color: color(accent-color);
  transition: transform 0.7s, background 0.2s ease-in;

  &:before,
  &:after {
    background-color: color(accent-color);
    position: absolute;
    content: "";
    height: 3px;
    width: 24px;
  }

  &:before {
    inset: -9px auto auto 0;
  }
  &:after {
    inset: 9px auto auto 0;
  }
}

.nav__container {
  z-index: 11;
  position: fixed;
  top: 0;
  right: 0;
  min-height: 100vh;
  width: 254px;
  padding-top: 7.375rem;
  padding-left: 2rem;
  // Workaround for backrop-filter: blur();
  background: linear-gradient(
    to right,
    rgb(87, 87, 87) 10%,
    rgb(194, 194, 194) 100%
  );
  @supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
    background: rgba(255, 255, 255, 0.05);
    -webkit-backdrop-filter: blur(32px);
    backdrop-filter: blur(32px);
  }

  transform: translateX(100%);
  transition: transform 0.5s ease-in-out;

  &.open {
    transform: translateX(0);
  }
}

.nav__list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-flow: column;
  row-gap: 2rem;
}

.nav__item {
  position: relative;
  flex-basis: 100%;

  &:before {
    // display: none;
    background: color(secondary-color);
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    right: 0;
    width: 4px;
    background: color(secondary-color);
    transform: scaleY(0);
    transition: all 0.1s ease-in;
  }

  &:hover,
  &:focus {
    &:before {
      display: block;
      transform: scaleY(1);
      opacity: 0.5;
    }
  }
}
.active {
  &:before {
    transform: scaleY(1);
  }

  &:hover,
  &:focus {
    &:before {
      transform: scaleY(1);
      opacity: 1;
    }
  }
}

.nav__link {
  text-transform: uppercase;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  text-decoration: none;
  color: color(secondary-color);
}

.nav__number {
  font-weight: bold;
  margin-right: 10px;
}

@media (min-width: 768px) {
  .header {
    padding: 1.5rem 39px;
    @media (min-width: 1100px) {
      padding: 1.5rem 0 1.5rem 55px;
      margin-top: 40px;
    }
  }

  .header__logo {
    width: 48px;
    height: 48px;
  }

  .hamburger__container {
    display: none;
  }
  .nav__container {
    transform: translateX(0);
    margin: 0;
    padding: 39px 48px;
    position: absolute;
    inset: 0 0 auto auto;
    height: 96px;
    min-height: auto;
    width: auto;

    @media (min-width: 1100px) {
      width: 57.63%;
      padding-left: 4.6%;
      padding-right: 0;

      @media (min-width: 1200px) {
        padding-left: 8.6%;
      }

      &:before {
        position: absolute;
        content: "";
        top: 48px;
        height: 1px;
        left: -53%;
        width: calc(53% + 30px);
        background: color(secondary-color);
        opacity: 0.25;
      }
    }
  }

  .nav__list {
    margin: 0;
    flex-direction: row;
    column-gap: 37px;
    row-gap: 0;

    @media (min-width: 1000px) {
      column-gap: 48px;
    }
  }

  .nav__item {
    flex-basis: auto;

    &:before {
      content: "";
      inset: auto auto -39px 0;
      height: 4px;
      width: 100%;
      background: color(secondary-color);
      transform: scaleX(0);
    }

    &:hover,
    &:focus {
      &:before {
        display: block;
        transform: scaleX(1);
        opacity: 0.5;
      }
    }
  }

  .active {
    &:before {
      transform: scaleX(1);
    }

    &:hover,
    &:focus {
      &:before {
        transform: scaleX(1);
        opacity: 1;
      }
    }
  }

  .nav__link {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;

    @media (min-width: 1100px) {
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 2.7px;
    }
  }

  .nav__number {
    display: none;

    @media (min-width: 1100px) {
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 2.7px;
      display: inline-block;
    }
  }
}
